$gridPercentSwitch:true;
@import "e:/tobe/function";
@charset "UTF-8";
.wrap-page,
body,
html {
    height: 100%
}
body {
    font:12px/1.5 "Microsoft YaHei",tahoma,arial,'Hiragino Sans GB','宋体',sans-serif
}
.ipt,
button:focus {
    outline: 0
}

*,
.ipt {
    -webkit-appearance: none
}

.bye-box,
.hongbao-info,
.info-box,
.jiang-type,
.welcome1,
.welcome2 {
    font-weight: 700;
    color: #1A2B5C;
}

.child2 {
    float: left;
    width: 50%;
    text-align: center
}

* {
    margin: 0;
    padding: 0;
    -webkit-box-sizing: border-box;
    box-sizing: border-box
}

button {
    border: none
}

.wrap-page {
    -webkit-background-origin: content-box;
    -webkit-overflow-scrolling: touch;
    background: #ffffff;
    background-size: 100% auto;
    text-align: center;
    display: table;
    width: 100%
}

.page {
    display: table-cell;
    height: 100%;
    width: 100%;
    vertical-align: middle
}

.inner {
    margin: 0 .78125rem
}
.hide{
    display: none;
}
#page1 {
    background: url(../images/page1-bg.png) center bottom no-repeat;
    background-size: 100% 100%
}

#page2 {
    background: url(../images/page1-bg.png) center bottom no-repeat;
    background-size: 100% 100%
}

#page3 {
    background: url(../images/page3-bg.png) center bottom no-repeat;
    background-size: 100% 100%;
}
#page4 {
    background: url(../images/page3-bg.png) center bottom no-repeat;
    background-size: 100% 100%
}
#page5 {
    background: url(../images/page1-bg.png) center bottom no-repeat;
    background-size: 100% 100%
}
#page6 {
    background: url(../images/page1-bg.png) center bottom no-repeat;
    background-size: 100% 100%
}
.icon{
    width:1.34375rem;
    height: 1.34375rem;
    position: absolute;
    right:1.28125rem;
    top:0.625rem;
    background:url(../images/icon.png) no-repeat center center;
    background-size:100% 100%;
}
.pageText1{
    width:5.46875rem;
    height: 1.875rem;
    position:absolute;
    left: 0.9375rem;
    top:1.875rem;
    .p1{
        font-size:0.625rem;
        color:#fff;
        text-align:left;
    }    
    .p2{
        font-size:0.625rem;
        color:#fff;
        text-align:left;
        .money{
            color:red;
        }
    }
}
.earth{
    width:7.890625rem;
    height: 8.328125rem;
    position:absolute;
    left: 0.9375rem;
    top:4.53125rem;
    background:url(../images/earth.png) no-repeat center center;
    background-size:100% 100%;
}
.count{
    display: block;
    width:5.3125rem;
    height: 1.09375rem;
    text-align: center;
    color:#fff;
    background-color:#D90000;
    position:absolute;
    left:50%;
    margin-left: -2.65625rem;
    bottom:1.015625rem;
    line-height:1.09375rem;
    text-decoration: none;
    font-size:0.5rem;
}
#page2{
  .icon{
      left: 0.625rem
  }
  .contentIcon{
    width:4.640625rem;
    height: 3.328125rem;
    background:url(../images/page2-content.png) no-repeat center center;
    background-size:100% 100%;
    position:absolute;
    left: 2.6875rem;
    top:3.6875rem;
  }
  .tel{
    width:5.0rem;
    height: 1.015625rem;
    display: block;
    position:absolute;
    left:2.5rem;
    top:10.5625rem;
    background:#fff;
    text-align: center;
    line-height:1.015625rem;
    text-decoration: none;
    font-size:0.46875rem;
  }
  .click{
    width:5.0rem;
    height: 1.015625rem;
    display: block;
    position:absolute;
    left:2.5rem;
    top:11.875rem;
    background:#D90000;
    text-align: center;
    line-height:1.015625rem;
    text-decoration: none;
    font-size:0.46875rem;
    color:#fff;
  }
  .text{
    color:#fff;
    font-size:0.390625rem;
    position:absolute;
    left: 2.3875rem;
    width:5.2rem;
    top:7.65625rem;
    height: 0.46875rem;
  }
} 
#page3{
  .icon{
      left: 0.625rem
  }
  .content1{
    background:url(../images/page3-content1.png) no-repeat center center;
    background-size:100% 100%;
    position:absolute;
    width:3.484375rem;
    height: 2.890625rem;
    left: 3.28125rem;
    top:1.875rem; 
  }
  .price{
    width:5.78125rem;
    height: 0.625rem;
    position:absolute;
    left: 2.109375rem;
    top:5.15625rem;
    text-align:center;
    font-size:0.5rem;
    color:#fff;
    font-weight:600;
    .money{
      color:red;
    }
  }
  .use{
      border:2px solid #fff;
      color:#fff;
      width:3.96875rem;
      position:absolute;
      top:6.09375rem;
      left: 3.015625rem;
      height: 0.5625rem;
    }
    .content2{
      background:url(../images/page3-content2.png) no-repeat center center;
      background-size:100% 100%;
      position:absolute;
      width:3.96875rem;
      height:5.28125rem;
      left: 3.015625rem;
      top:6.834375rem;      
    }
    .useTime{
      position:absolute;
      width:3.953125rem;
      height: 0.859375rem;
      left: 3.015625rem;
      top:12.296875rem; 
      .time{
        font-size:0.3125rem;
        color:#fff;
        text-align:center;
      }        
      .where{
        font-size:0.3125rem;
        color:#fff;
        text-align:center;
      }            
    }
    .more{
      display: block;
      position:absolute;
      width:4.6875rem;
      height: 0.875rem;
      left: 2.65625rem;
      top:12.296875rem;  
      top:13.4375rem; 
      text-decoration: none;    
      font-size:0.46875rem;
      background-color:#D90000;
      color:#fff;  
      line-height: 0.875rem;
    }
}
#page4{
