        height: 1.65625rem;
        float: left;
        margin-top:0.734375rem;
        background:url(../images/page4-step2.png) no-repeat;
        background-size:100% 100%;
        margin-left:0.859375rem;
      }
      .right{
        float:left;
        margin-left:0.46875rem;
        p{
          text-align:left;
          color:#fff;
        }
        .p1{
          
          font-size:1.0625rem;
        }
        .p2{
          font-size:0.328125rem;
        }
        .p3{
          font-size:0.328125rem;
          .special{
            color:red;
          }          
        }
      }
    }     
    .step3{
      .left{
        width:3.359375rem;
        height: 1.65625rem;
        float: left;
        margin-top:0.734375rem;
        background:url(../images/page4-step3.png) no-repeat;
        background-size:100% 100%;
        margin-left:0.859375rem;
      }
      .right{
        float:left;
        margin-left:0.46875rem;
        p{
          text-align:left;
          color:#fff;
        }
        .p1{
          
          font-size:1.0625rem;
        }
        .p2{
          font-size:0.328125rem;
        }
        .p3{
          font-size:0.328125rem;
          .special{
            color:red;
          }          
        }
      }
    }    
  }
}
#page5{
  .icon{
    left:1.25rem;
  }
  .content{
    width:7.015625rem;
    height: 12.578125rem;
    background:url(../images/page5-content.png) no-repeat;
    background-size:100% 100%;
