      left:1.203125rem;
      line-height: 0.9375rem;
      font-size:0.34375rem;
    }
    .text{
      margin-top:4.0625rem;
      p{
        color:#fff;
        width:5.578125rem;
      }
      .p1{
        font-size:0.34375rem;
      }
      .p2{
